:root{
  --color-primary: #eb2f64;
  --color-primary-dark: #ff3366;
  --color-primary-light: #ba265d;
  
  --color-grey-light-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #ccc;
  
  --color-grey-dark-1: #333;
  --color-grey-dark-2: #777;
  --color-grey-dark-4: #999;

  --shadow-dark: 0 10rem 20rem rgba(0,0,0,0.3);
  --shadow-light: 0 2rem 5rem rgba(0,0,0,.06);

  --line: 1px solid var(--color-grey-light-2);  
  }

  $bp-largest: 75em; //1200px
  $bp-large: 68.75em; //1100px
  $bp-medium: 56.25em; //900px
  $bp-small: 37.5em; //600px
  $bp-smallest: 31.25em; //500px
  
  
  * {
    margin: 0;
    padding: 0;
  }
  
  
  *,
  *::after,
  *::before {
    box-sizing: inherit;
  }
  
  html{
    box-sizing: border-box;
    font-size: 62.5%;

    @media only screen and (max-width: $bp-large){
      font-size: 50%;
    }
  }
  
  body{
      font-family: 'Open Sans', sans-serif;
      font-weight: 400;
      line-height: 1.6;
      color: var(--color-grey-dark-2);
      background-image: linear-gradient(to right bottom, var(--color-primary), var(--color-primary-dark));
      background-size: cover;

      min-height: 100vh;
  }

